import React, { useReducer } from 'react';
import { color, font } from '../constants';
import styled from 'styled-components';
import pattern from '../images/pattern/1994698.ai.svg';
import { SEARCH_SERVICE } from '../static-info';
import { useMedia } from '../hooks';

const bigIcon = { width: '50px', height: '50px' };

const BackgroundPattern = styled.div`
  width: 100%;
  height: 100%;
  position: absolute;
  background-image: url(${pattern});
  background-repeat: repeat;
  top: 0;
  left: 0;
  ::before{
    position: absolute;
    background-color: ${color.prim};
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    content: " ";
    opacity: .92;
  }
`

const BookingCont = styled.div`
  height: 15vh;
  min-height: 100px;
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
  background: ${color.prim};
  @media(min-width: 576px){
    min-height: 30vh;
    padding-top: 5rem;
    padding-bottom: 2rem;
  }
`
const BookOptions = styled.div`
  width: 90%;
  height: 50%;
  position: absolute;
  top: -20%;
  background: ${color.prim};
  z-index: 15;
  display: flex;
  @media(min-width: 576px){
    width: 50%;
    padding-top: 2rem;
  }
`
const BookButton = styled.button`
  background-color: transparent;
  font-size: 9px;
  position: relative;
  z-index: 20;
  border: none;
  flex-grow: 1;
  width: 25%;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  padding-top: .5rem;
  outline: none;
  transition: 250ms ease;
  font-weight: bold;
  fill: ${({ active }) => active ? color.light : color.sec };
  color: ${({ active }) => active ? color.light : color.sec };
  &:focus{
    outline: none;
  };
  &:active{
    fill:${color.phantom};
    color: ${color.phantom}
  }
  &:hover{
    outline: none;
  };
  @media(min-width: 576px){
    font-size: 1rem;
    &:hover{
      fill:${color.light};
      color: ${color.light};
      outline: none;
    }
  }
`
const BookFom = styled.form`
  margin-top: 2rem;
  position: relative;
  z-index: 15;
  display: flex;
  justify-content: center;
  width: 80%;
  font-size: 11px;
  @media(min-width: 576px){
    font-size: 1rem;
    width: 60%;
  }
`
const BookLabel = styled.label`
  background-color: ${color.light};
  display: flex;
  flex-direction: column;
  width: 50%;
  margin: 0;
  position: relative;
  flex: 1 1 0;
`
const BookLabelTitile = styled.p`
  position: absolute;
  top: -50%;
  left: 0;
  color: ${color.sec};
  text-transform: capitalize;
  @media(min-width: 576px){
    top: -65%;
  }
`
const BookInput = styled.input`
  width: 100%;
  height: 100%;
  //border: 1px solid red;
  font-size: 11px;
  padding: .5rem .3rem;
  margin: 0;
  outline: none;
  @media(min-width: 576px){
    font-size: 1rem;
  }
`
const BookInpuLine = styled.div`
  background-color: ${color.sec};
  width: ${({ active }) => active ? '100%' : '0'};
  height: 3px;
  transition: ${({ active }) => active ? '500ms ease' : 'none'};
  position: absolute;
  left: 0;
  bottom: 0;
`
const BookFormButton = styled.button`
  background-color: ${color.sec};
  border: none;
  color: ${color.light};
  font-family: ${font.common};
  text-transform: uppercase;
  font-size: 11px;
  flex: 1 0 1;
  transition: 250ms ease;
  &:hover{
    background-color: ${color.light};
    color: ${color.sec};
  };
  &:active{
    background-color: rgba(0, 9, 0, .5);
    color: ${color.light};
  };
  @media(min-width: 576px){
    font-size: 1rem;
    padding: 0 1rem;
  }
`

export default ()=> {
  const movil = useMedia('xs');
  const [book, setBook] = useReducer((prev, next) => ({ ...prev, ...next }),{
    hotel: true,
    tour: false,
    transport: false,
    travel: false,
  })
  const handleBookOption = (option) => {
    setBook({
      hotel: option === 'hotel' ? true : false,
      tour: option === 'tour' ? true : false,
      transport: option === 'transport' ? true : false,
      travel: option === 'travel' ? true : false,
    })
  }

  const [inputActive, serInputActive] = useReducer((prev, next) => ({ ...prev, ...next }), {
    where: false,
    date: false,
    persons: false,
  })
  const handleInputActive = (input) => {
    serInputActive({
      where: input === 'where' ? true : false,
      date: input === 'date' ? true : false,
      persons: input === 'persons' ? true : false,
    })
  }

  return(
    <BookingCont id="panel">
      <BackgroundPattern />
      <BookOptions>
        <BackgroundPattern />
        {
          SEARCH_SERVICE.map(({id, Icon, text, name}) => (
            <BookButton key={id} active={book[name]} onClick={()=> handleBookOption(name)}>
              <Icon style={!movil ? bigIcon : null} />
              {text}
            </BookButton>
          ))
        }
      </BookOptions>
      <BookFom onSubmit={(e) => e.preventDefault()}>
        <BookLabel>
          <BookLabelTitile>where to</BookLabelTitile>
          <BookInput
            onFocus={()=> handleInputActive(('where'))}
            placeholder="venice"
          />
          <BookInpuLine active={inputActive.where} />
        </BookLabel>
        <BookLabel>
          <BookLabelTitile>date to start</BookLabelTitile>
          <BookInput
            onFocus={()=> handleInputActive(('date'))}
            placeholder="yyy-mm-dd"
          />
          <BookInpuLine active={inputActive.date} />
        </BookLabel>
        <BookLabel>
          <BookLabelTitile>persons</BookLabelTitile>
          <BookInput
            onFocus={()=> handleInputActive(('persons'))}
            placeholder="3"
          />
          <BookInpuLine active={inputActive.persons} />
        </BookLabel>
        <BookFormButton>find now</BookFormButton>
      </BookFom>
    </BookingCont>
  )
}
