import React, { useEffect } from 'react';
import { color, lorem, font } from '../constants';
import styled from 'styled-components';
import { Container, Row, Col } from 'react-bootstrap';
import Button from './button';
import { DESTINATIONS } from '../static-info';
import { useObserver } from '../hooks';
import { gsap } from 'gsap';

const MainCont = styled.div`
  background-color: ${color.light};
  padding: 4rem 0;
  position: relative;
`
const TitleLine = styled.div`
  position: absolute;
  top: 0;
  left: 50%;
  width: 5px;
  background-color: ${color.sec};
  height: 0;
`
const TitleCont = styled.div`
  margin-top: 2rem;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  text-transform: capitalize;
  text-align: center;
`
const Title = styled.h2`
  opacity: 0;
  animation-delay: 1s;
`
const TitleInfo = styled.p`
  opacity: 0;
  text-align: center;
  margin-bottom: 4rem;
  animation-delay: 1.5s;
  @media(min-width: 576px){
    width: 50%;
  }
`
const MediaCont = styled.div`
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  animation-delay: ${({ delay })=> delay}s;
  opacity: 0;
`
const MediaImg = styled.div`
  width: 60px;
  height: 60px;
  border-radius: 50%;
  background-color: ${color.prim};
  background-image: url(${({ src }) => src});
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;
  font-size: 11px;
  box-shadow: 0px 1px 1px rgba(0, 0, 0, .12),
              0px 2px 2px rgba(0, 0, 0, .12),
              0px 4px 4px rgba(0, 0, 0, .12),
              0px 8px 8px rgba(0, 0, 0, .12),
              0px 16px 16px rgba(0, 0, 0, .12);
  @media(min-width:576px){
    width: 100px;
    height: 100px;
  }
`
const MediaTitle = styled.p`
  margin: 0;
  margin-top: .5rem;
  font-size: bold;
  text-transform: capitalize;
  font-family: ${font.common};
  font-size: 11px;
  @media(min-width:576px){
    font-size: 1rem;
  }
`
const MediaSubTitle = styled(MediaTitle)`
  margin: 0;
  font-weight: normal;
  font-family: inherit;
  font-size: 11px;
  @media(min-width:576px){
    font-size: 1rem;
  }
`
const Form = styled.form`
  margin-top: 2rem;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  width: 100%;
  opacity: 0;
`
const FormHeader = styled.div`
  display: flex;
`
const FormTitlePart1 = styled.p`
  font-weight: bold;
  //font-size: 2rem;
  font-size: 1.2rem;
  text-transform: capitalize;
`
const FormTitlePart2 = styled.p`
  font-weight:300;
  //font-size: 2rem;
  font-size: 1.1rem;
  margin-left: .5rem;
  text-transform: capitalize;
`
const FormBody = styled.div`
  display: flex;
  width: 100%;
  align-items: center;
  padding: 8px;
  border-radius: 30px;
  border: 1px solid #ccc;
  @media(min-width: 576px){
    width: 50%;
  }
`
const Input = styled.input`
  border: none;
  outline: none;
  padding-left: 1rem;
  height: 100%;
  width: 100%;
  flex: 1 1 0;
  margin-right: 1rem;
`
const Media = ({ id, title, subTitle, img, delay, className }) => (
  <MediaCont className={className} delay={delay}>
    <MediaImg src={img} />
    <MediaTitle>{title}</MediaTitle>
    <MediaSubTitle>{subTitle}</MediaSubTitle>
  </MediaCont>
)
export default ()=> {
  const [observed, setObserved] = useObserver();
  useEffect(()=>{
    if(observed){
      gsap.to('#destinationsLine', 1.5, { height: '5rem' });
    }else{
      gsap.set("#destinationsLine", { height: 0 });
    }
  },[observed]);
  return(
    <MainCont ref={el => setObserved(el)} id="destinations">
      <TitleLine id="destinationsLine" />
      <Container>
        <TitleCont>
          <Title className={observed && "animated fadeIn"}>choose destination</Title>
          <TitleInfo className={observed && "animated fadeIn"}>{lorem.lg}</TitleInfo>
        </TitleCont>
        <Row>
          {
            DESTINATIONS.map((item, index) => <Col xs={4} sm={2} key={item.id}><Media delay={(index + 2) / 2} className={observed && "animated fadeIn"} {...item} /></Col>)
          }
          <Col xs={12}>
            <Form className={observed && "animated fadeInUp delay-4s"} onSubmit={e => e.preventDefault()}>
              <FormHeader>
                <FormTitlePart1>subscribe</FormTitlePart1>
                <FormTitlePart2>to the newsletter</FormTitlePart2>
              </FormHeader>
              <FormBody>
                <Input
                  placeholder="Enter Your Email"
                />
                <Button>subscribe</Button>
              </FormBody>
            </Form>
          </Col>
        </Row>
      </Container>
    </MainCont>
  )
}
