import React from 'react';
import { v1 as uuid } from 'uuid';
import { color, lorem } from '../constants';
import styled from 'styled-components';
import contactBackground from '../images/contact-media.jpg';
import { Container, Row, Col } from 'react-bootstrap';
import { ReactComponent as Arrow } from '../images/icons/right-arrow.svg';
import { ReactComponent as Map } from '../images/icons/map-marker-alt.svg';
import { ReactComponent as Plane } from '../images/icons/paper-plane.svg';
import { ReactComponent as Phone } from '../images/icons/phone-alt.svg';
import { ReactComponent as Facebook } from '../images/icons/facebook-square.svg';
import { ReactComponent as Twitter } from '../images/icons/twitter-square.svg';
import { ReactComponent as Instagram } from '../images/icons/instagram.svg';
import { ReactComponent as Youtube } from '../images/icons/youtube-square.svg';
import Logo from './logo';
import post1 from '../images/posts/post1.jpg';
import post2 from '../images/posts/post2.jpg';
import post3 from '../images/posts/post3.jpg';

const POSTS = [post1, post2, post3];

const ContactCont = styled.div`
  user-select: none;
  padding: 4rem 0;
  background-color: rgba(0, 0, 0, .5);
  background-image: url(${contactBackground});
  background-position: center;
  background-size: cover;
  background-repeat: no-repeat;
  background-blend-mode: multiply;
  color: ${color.light};
  //font-size: 14px;
  @media(min-width: 576px){
    //font-size: 1rem;
  }
`
const ContactItem = styled.div`
  margin-bottom: 1rem;
`
const Title = styled.div`
  font-size: 1.5rem;
  margin: 1rem 0;
  text-transform: capitalize;
  font-weight: bold;
`
const Paragraph = styled.p`
  text-align: justify;
`
const MediaIconCont = styled.div`
  display: flex;
  align-items: center;
`
const MediaIconInfo = styled(Paragraph)`
  margin: 0;
  margin-left: .5rem;
`
const ContactForm = styled.form`
  border: 1px solid ${color.light};
  display: flex;
  background: transparent;
  margin-bottom: 1rem;
`
const ContactInput = styled.input`
  flex: 1;
  width: 100%;
  margin: 0;
  border: none;
  padding: .5rem;
  background: transparent;
  color: ${color.light};
  outline: none !important;
`
const ContactButton = styled.button`
  outline: none !important;
  border: none;
  background: ${color.light};
  padding: 1rem;
  fill: ${color.sec};
  &:active{
    background: ${color.phantom};
    color: ${color.sec};
  }
`
const SocialCont = styled.div`
  display: flex;
`
const SocialLink = styled.a`
  margin-right: 1rem;
  text-decoration: none !important;
  fill: ${color.light};
  transition: 250ms ease;
  &:hover{
    fill: ${color.prim};
    cursor: pointer;
  }
  &:active{
    fill: ${color.primDark};
  }
`
const MediaCont = styled.div`
  display: flex;
  border-bottom: 2px solid ${color.light};
  padding-bottom: .5rem;
  margin-bottom: 1rem;
  cursor: pointer;
  transition: 250ms ease;
  &:hover{
    color: ${color.prim};
  }
`
const MediaImage = styled.div`
  width: 50%;
  background-color: ${color.light};
  background-image: url(${({ src }) => src});
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;
  margin-right: .5rem;
`
const Media = ({ children, src }) => (
  <MediaCont>
    <MediaImage src={src}/>
    <Paragraph>{children}</Paragraph>
  </MediaCont>
)
const Social = ({ items }) => (
  <SocialCont>
    {
      items.map(Icon => <SocialLink key={uuid()}><Icon width={40} height={40} /></SocialLink>)
    }
  </SocialCont>
);

const NewsLetter = ()=> (
  <ContactForm onSubmit={e => e.preventDefault()}>
    <ContactInput />
    <ContactButton>
      <Arrow />
    </ContactButton>
  </ContactForm>
)

const MediaIcon = ({ Icon, children }) => (
  <MediaIconCont>
    <Icon width={20} height={20} fill={color.light} />
    <MediaIconInfo>{children}</MediaIconInfo>
  </MediaIconCont>
);

export default ()=> {

  return(
    <ContactCont id="contact">
      <Container>
        <Row>
          <Col xs={12} sm={4}>
            <ContactItem>
              <Title><Logo /></Title>
              <Paragraph>{lorem.sm}</Paragraph>
              <MediaIcon Icon={Map}>23 web dev streer, Venezuela</MediaIcon>
              <MediaIcon Icon={Phone}>(+58) 0412 0598647</MediaIcon>
              <MediaIcon Icon={Plane}>felixtineo05@gmail.com</MediaIcon>
            </ContactItem>
          </Col>
          <Col xs={12} sm={4}>
            <ContactItem>
              <Title>Newsletter</Title>
              <Paragraph>{lorem.sm}</Paragraph>
              <NewsLetter />
              <Social items={[Facebook, Instagram, Twitter, Youtube]} />
            </ContactItem>
          </Col>
          <Col xs={12} sm={4}>
            <Title>Latest Post</Title>
            {
              POSTS.map(place => <Media src={place}>{lorem.xs}</Media>)
            }
          </Col>

        </Row>
      </Container>
    </ContactCont>
  )
}
