import React from 'react';
import { color } from '../constants';
import { NAV } from '../static-info';
import styled from 'styled-components';

const NavCont = styled.nav`
  display: flex;
`
const NavLink = styled.a`
  position: relative;
  color: ${color.light};
  outline: none !important;
  text-decoration: none !important;
  transition: 250ms ease;
  padding-right: 2rem;
  text-transform: capitalize;
  &:visited{
    color: ${color.phantom};
  };
  &:hover{
    color: ${({ down }) => down ? color.sec  : color.primLight};
  };
  &:active{
    color: ${color.phantom};
  };
`
const PhantomLine = styled.div`
  width: 100%;
  height: 2px;
  background-color: ${color.light};
  position: absolute;
  left: 0;
  bottom: -.8rem;
  z-index: 1;
`
const ActiveLine = styled(PhantomLine)`
  width: 0;
  background-color: ${({ down }) => down ? color.sec  : color.primLight};
  opacity: 0;
  z-index: 2;
  transition: width 500ms ease;
  ${NavLink}:hover &{
    opacity: 1;
    width: 100%;
  }
`
export default({ down })=>(
  <NavCont>
    {
      NAV.map(({href, text, id}) => (
        <NavLink down={down} key={id} href={href}>
          {text}
          <PhantomLine />
          <ActiveLine down={down} />
        </NavLink>
      ))
    }
  </NavCont>
);
