import React from 'react';
import heroImg from '../images/hero-1.jpg';
import heroVideo from '../videos/video-hd.mp4';
import styled from 'styled-components';
import { color, font, lorem } from '../constants';
import { Container } from 'react-bootstrap';
import Button from './button';

const HeroVideo = styled.video`
  position: absolute;
  top: 0;
  left: 0;
  object-fit: cover;
  object-position: center;
  width: 100%;
  height: 100vh;
`
/*const HeroImage = styled.div`
  width: 100%;
  height: 100vh;
  position: absolute;
  top: 0;
  left: 0;
  background-image: url(${heroImg});
  background-size: cover;
  background-repeat: no-repeat;
  background-position: cover;
`*/
const HeroCont = styled.div`
  position: relative;
  height: 100vh;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  color: ${color.light};
`
const Title = styled.h1`
  font-family: ${font.hero};
  text-transform: capitalize;
  margin: 0;
`
const SubTitle = styled.p`
  margin-bottom: 2rem;
  text-align: center;
  font-size: 11px;
  @media(min-width: 576px){
    font-size: 1rem;
  }
`

export default ()=> {
  const onBook = (e) =>{
    const el = document.getElementById('panel');
    window.scrollTo({ top: el.offsetTop - 90 });
  }
  return(
    <React.Fragment>
    <HeroVideo playsInline autoPlay muted loop poster={heroImg} id="home">
      <source src={heroVideo} type ="video/mp4" />
    </HeroVideo>
      <Container>
        <HeroCont>
          <Title className="animated fadeIn delay-2s">eternally yours</Title>
          <SubTitle className="animated fadeIn delay-3s">{lorem.md}</SubTitle>
          <Button
           className="animated fadeIn delay-4s"
           onClick={onBook}
          >
           start tour</Button>
        </HeroCont>
      </Container>
    </React.Fragment>
  )
}
