import React from 'react';
import { color, font } from '../constants';
import styled from 'styled-components';

const Button = styled.button`
  background-color: ${({ dark }) => dark ? color.sec : color.prim};
  color: ${color.light};
  border: none;
  padding: 8px 20px;
  border-radius: 20px;
  transition: 250ms ease;
  text-transform: uppercase;
  font-weight: bold;
  outline: none !important;
  font-family: ${font.common};
  &:hover{
    outline: none;
    color: ${({ dark }) => dark ? color.light : color.sec};
    background-color: ${({ dark }) => dark ? "#555" : color.primLight};
  }
  &:active{
    outline: none;
    background-color: ${({ dark }) => dark ? color.dark : color.primDark};
    color: ${color.phantom};
  }
`

