import React from 'react';
import { color } from '../constants';
import styled from 'styled-components';
import { Container, Row, Col, Carousel } from 'react-bootstrap';
import { DESTINATIONS } from '../static-info';
//import { ReactComponent as ArrowRight } from '../images/icons/right-arrow.svg';
import { useMedia } from '../hooks';

const MediaCont = styled.div`
  width: 100%;
  padding-top: 75%;
  background-image: url(${({ src }) => src});
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;
  border-radius: 2%;
  overflow: hidden;
  box-shadow: 0px 1px 1px rgba(0, 0, 0, .12),
              0px 2px 2px rgba(0, 0, 0, .12),
              0px 4px 4px rgba(0, 0, 0, .12),
              0px 8px 8px rgba(0, 0, 0, .12),
              0px 16px 16px rgba(0, 0, 0, .12);
`
const MediaInfoCont = styled.div`
  background-color: ${color.light};
  padding: .8rem;
  color: ${color.sec};
  text-align: left;
`
const MediaInfoTitle = styled.p`
  margin: 0;
  font-weight: bold;
  //font-size: 11px;
`
const MediaInfoText = styled(MediaInfoTitle)`
  font-weight: normal;
`
/*const NextIcon = styled.div`
  padding: .5rem;
  border: 10px solid ${color.prim};
  border-top-right-radius: 5px;
  border-bottom-right-radius: 5px;
  fill: ${color.prim};
`*/
const Media = ({ img, infoTitle, infoText }) => (
  <MediaCont src={img}>
    <MediaInfoCont>
      <MediaInfoTitle>{infoTitle}</MediaInfoTitle>
      <MediaInfoText>{infoText}</MediaInfoText>
    </MediaInfoCont>
  </MediaCont>
);


export default ()=> {
  const isMovil = useMedia('xs');

  if(isMovil){
    return(
      <Carousel
        indicators={false}
      >
        {
          DESTINATIONS.map(item => (
            <Carousel.Item key={item.id}>
              <Col>
                <Media {...item}/>
              </Col>
            </Carousel.Item>
          ))
        }
      </Carousel>
    )
  }
  return(
    <Container>
      <Carousel>
        <Carousel.Item>
          <Row>
            {
              DESTINATIONS.slice(0,3).map(item => (
                <Col xs={4} key={item.id}>
                  <Media {...item}/>
                </Col>
              ))
            }
          </Row>
        </Carousel.Item>
        <Carousel.Item>
          <Row>
            {
              DESTINATIONS.slice(3,6).map(item => (
                <Col xs={4}>
                  <Media {...item}/>
                </Col>
              ))
            }
          </Row>
        </Carousel.Item>
      </Carousel>
    </Container>
  )
}
