import React, { useEffect } from 'react';
import { color, font } from '../constants';
import styled from 'styled-components';
import pattern from '../images/pattern/1994698.ai.svg';
import { ReactComponent as Quotation } from '../images/icons/quote-left.svg';
import { REVIEWS } from '../static-info';
import { Container, Row, Col } from 'react-bootstrap';
import Loop from './loop';
import { useObserver } from '../hooks';
import { gsap } from 'gsap';


const MainCont = styled.div`
  background-color: ${color.light};
  padding: 4rem 0;
  position: relative;
  overflow: hidden;
`
const BackgroundPattern = styled.div`
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0;
  left: 0;
  background-image: url(${pattern});
  background-repeat: repeat;
  ::before{
    position: absolute;
    background-color: ${color.light};
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    content: " ";
    opacity: .80;
  }
`

const TitleLine = styled.div`
  position: absolute;
  top: 0;
  left: 50%;
  width: 5px;
  background-color: ${color.sec};
  height: 0;
`
const TitleCont = styled.div`
  position: relative;
  z-index: 5;
  margin-top: 2rem;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  text-transform: capitalize;
  text-align: center;
  color: ${color.sec};
  margin-bottom: 2rem;
`
const Title = styled.h2`
  opacity: 0;
  animation-delay: 1s;
`
const MediaCont = styled.div`
  display: flex;
  align-items: center;
  margin-bottom: 2rem;
`
const MediaImg = styled.div`
  margin-right: 1rem;
  width: 60px;
  height: 60px;
  border-radius: 50%;
  background-color: ${color.prim};
  background-image: url(${({ src }) => src});
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;
  font-size: 11px;
  box-shadow: 0px 1px 1px rgba(0, 0, 0, .12),
              0px 2px 2px rgba(0, 0, 0, .12),
              0px 4px 4px rgba(0, 0, 0, .12),
              0px 8px 8px rgba(0, 0, 0, .12),
              0px 16px 16px rgba(0, 0, 0, .12);
  @media(min-width:576px){
    width: 100px;
    height: 100px;
  }
`
const MediaTitle = styled.p`
  margin: 0;
  margin-top: .5rem;
  font-size: bold;
  text-transform: capitalize;
  font-family: ${font.common};
  font-size: 11px;
  @media(min-width:576px){
    font-size: 1rem;
  }
`
const MediaSubTitle = styled(MediaTitle)`
  margin: 0;
  font-weight: normal;
  font-family: inherit;
  font-size: 11px;
  @media(min-width:576px){
    font-size: 1rem;
  }
`
const ReviewCont = styled.div`
  display: flex;
  flex-direction: column;
  animation-delay: 1.5s;
  opacity: 0;
`
const ReviewInfoCont = styled.div`
  padding: 2rem 0;
`
const ReviewTitle = styled.p`
  font-family: ${font.common};
`
const ReviewInfo = styled.p`
  text-align: justify;
`

const Media = ({ id, title, subTitle, img }) => (
  <MediaCont>
    <MediaImg src={img} />
    <div>
      <MediaTitle>{title}</MediaTitle>
      <MediaSubTitle>{subTitle}</MediaSubTitle>
    </div>
  </MediaCont>
);

const Review = ({ title, review, name, subInfo, img, className, observed }) => (
  <ReviewCont className={observed && className}>
    <Quotation fill={color.prim} width="60" height="60" />
    <ReviewInfoCont>
      <ReviewTitle>{title}</ReviewTitle>
      <ReviewInfo>
        {review}
      </ReviewInfo>
    </ReviewInfoCont>
    <Media
      title={name}
      subTitle={subInfo}
      img={img}
    />
  </ReviewCont>
)


export default ()=> {
  const [observed, setObserved] = useObserver();
  useEffect(()=> {
    if(observed){
      gsap.to('#reviewLine', 1.5, { height: "5rem" });
    } else {
      gsap.set('#reviewLine', { height: 0 });
    }
  },[observed])
    return(
      <MainCont ref={el => setObserved(el)} id="reviews">
        <BackgroundPattern />
        <TitleLine id="reviewLine" />
        <TitleCont>
          <Title className={ observed && 'animated fadeIn' }>travelers reviews</Title>
        </TitleCont>
        <Container>
          <Row>
            {
              REVIEWS.map((rev, index) => <Col key={rev.id} xs={12} sm={6}><Review observed={observed} className={index%2 === 0 ? "animated fadeInLeft" : "animated fadeInRight"} {...rev}/></Col>)
            }
          </Row>
        </Container>
        <div className={observed && "animated fadeIn delay-2s"}>
          <Loop />
        </div>
      </MainCont>
  )
}
