import React from 'react';
import { color } from '../constants';
import styled from 'styled-components';
import { Container } from 'react-bootstrap';
import Logo from './logo';
import { NAV } from '../static-info';

const DrawerCont = styled.div`
  position: absolute;
  z-index: 250;
  transition: 500ms ease;
  right: ${({ visible }) => visible ? 0 : '-100%'};
  top: 0;
  width: 80%;
  height: 100vh;
  animation-duration: 500ms;
  background-color: ${color.sec};
`
const DrawerHeader = styled.header`
  display: flex;
  align-items: center;
  justify-content: space-between;
  color: ${color.prim};
  height: 4rem;
  border-bottom: 4px solid ${color.pim};
`
const DrawerCloseButton = styled.button`
  background: transparent;
  outline: none;
  border: 1px solid ${color.prim};
  color: ${color.prim};
  &:active{
    background: ${color.phantom};
    outline: none;
  }
  &:hover{
    outline: none;
  }
`
const DrawerNavButton = styled.button`
  background: transparent;
  width: 100%;
  text-align: left;
  border: none;
  border-bottom: 1px solid ${color.phantom};
  padding: 1rem 0;
  color: ${color.light};
  outline: none;
  &:active{
    background: ${color.phantom};
    color: ${color.prim};
    outline: none;
  };
  &:hover{
    outline: none;
  };
`

export default ({ visible, setVisible })=> {

  const onScroll = (section) => {
    const el = document.getElementById(section);
    if(el){
      setVisible(false);
      window.scrollTo({ top: el.offsetTop - 60 });
    }
    setVisible(false);
  }
  return(
    <DrawerCont visible={visible}>
      <Container>
        <DrawerHeader>
          <Logo />
          <DrawerCloseButton onClick={()=> setVisible(false)}>
            X
          </DrawerCloseButton>
        </DrawerHeader>
        {
          NAV.map(nav => <DrawerNavButton onClick={()=>onScroll(nav.text)} key={nav.id}>{nav.text}</DrawerNavButton>)
        }
      </Container>
    </DrawerCont>
  )
}
