const LoopCont = styled.div`
  height: 30vh;
  width: 100%;
  position: relative;
  overflow: hidden;
  padding: 2rem 0;
`
const Media = styled.div`
  position: absolute;
  height: 100%;
  background-image: url(${({ src }) => src});
  background-position: center;
  background-size: cover;
  background-repeat: no-repeat;
  box-shadow: 0px 1px 1px rgba(0, 0, 0, .12),
              0px 2px 2px rgba(0, 0, 0, .12),
              0px 4px 4px rgba(0, 0, 0, .12),
              0px 8px 8px rgba(0, 0, 0, .12),
              0px 16px 16px rgba(0, 0, 0, .12);
`

export default ()=> {

  useEffect(()=> {
    const tl = gsap.timeline();
    const containerWidth = window.innerWidth;
    const elementWidth = containerWidth / PLACES.length;

    tl.set('.media', {
      width: elementWidth,
      x: (index) => {
        return containerWidth;
      }
    })
    .to('.media', {
      duration: 10,
      x: (index) => {
        return -elementWidth;
      },
      ease: "none",
      stagger: {
        amount: 10,
        repeat: -1,
      },
    })
  },[])

  return(
      <LoopCont>
        {
          PLACES.map(src => <Media className="media" src={src} />)
        }
      </LoopCont>
  )
}
