import React, { useEffect } from 'react';
import { color, lorem } from '../constants';
import { TOURS } from '../static-info';
import styled from 'styled-components';
import { Container, Row, Col } from 'react-bootstrap';
import Button from './button';
import { useObserver } from '../hooks';
import { gsap } from 'gsap';

const PagesCont = styled.div`
  background-color: ${color.light};
  padding: 4rem 0;
  position: relative;
`
const TitleLine = styled.div`
  position: absolute;
  top: 0;
  left: 50%;
  width: 5px;
  background-color: ${color.sec};
  height: 0;
`
const TitleCont = styled.div`
  margin-top: 2rem;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
`
const Title = styled.h2`
  opacity: 0;
  animation-delay: .5s;
`
const TitleInfo = styled.p`
  text-align: center;
  margin-bottom: 4rem;
  opacity: 0;
  animation-delay: 1s;
  @media(min-width: 576px){
    width: 50%;
  }
`
const MediaCont = styled.div`
  opacity: 0;
  position: relative;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: flex-end;
  padding-top: 100%;
  background-image: url(${({ src }) => src});
  background-size: cover;
  background-repeat: no-repeat;
  background-position: center;
  margin-bottom: 2rem;
  padding-left: 1rem;
  user-select: none;
  animation-delay: ${({ delay }) => delay}s;
`
const MediaTitle = styled.p`
  color: ${color.light};
  font-weight: bold;
  margin: 0;
`
const MediaSubTitle = styled(MediaTitle)`
  color: ${color.prim};
  margin-bottom: 1rem;
`
const MediaInnerCont = styled.div`
  position: absolute;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  background-color: rgba(208, 214, 0, .8);
  opacity: 0;
  transition: 250ms ease;
  display: flex;
  flex-direction: column;
  justify-content: flex-end;
  align-items: flex-start;
  padding-left: 1rem;
  padding-bottom: 1rem;
  &:hover{
    opacity: 1;
  }
`
const MediaInnerInfo = styled.p`
  color: ${color.light};
`
const Media = ({ img, price, info, destiny, className, delay }) => (
  <MediaCont delay={delay} className={className}  src={img}>
    <MediaTitle>{destiny}</MediaTitle>
    <MediaSubTitle>{`$${price}`}</MediaSubTitle>
    <MediaInnerCont>
      <MediaInnerInfo>{info}</MediaInnerInfo>
      <Button dark>book now</Button>
    </MediaInnerCont>
  </MediaCont>
);

export default ()=> {
  const [observed, setObserved] = useObserver(0);

  useEffect(()=>{
    if(observed){
      gsap.to('#linePages', 1, { height:'5rem' });
    }else {
      gsap.to('#linePages', 1, { height: 0 });
    }
  },[observed])

  return(
    <PagesCont ref={el=> setObserved(el)} id="pages">
      <TitleLine id="linePages" />
      <Container fluid>
        <TitleCont>
          <Title className={`animated ${observed && 'fadeIn'}`}>
            Amazing Tours
          </Title>
          <TitleInfo className={`animated ${observed && 'fadeIn'}`}>{lorem.lg}</TitleInfo>
        </TitleCont>
        <Row>
            {
              TOURS.map((item, index) => <Col xs={12} sm={3}><Media className={`animated ${observed && `fadeIn`}`} delay={(index + 3) / 2} {...item}/></Col>)
            }
        </Row>
      </Container>
    </PagesCont>
  )
}
