import React, { useEffect } from 'react';
import { color } from '../constants';
import styled from 'styled-components';
import especial from '../images/especial.jpg';
import Carousel from './carousel';
import { useObserver } from '../hooks';
import { gsap } from 'gsap';

const MainCont = styled.div`
  background-color: rgba(0, 0, 0, .5);
  background-image: url(${especial});
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;
  background-blend-mode: multiply;
  height: 100vh;
  padding: 4rem 0;
  position: relative;
  display: flex;
  flex-direction: column;
  align-items: center;
  padding-top: 6rem;
`
const TitleLine = styled.div`
  position: absolute;
  top: 0;
  left: 50%;
  width: 5px;
  background-color: ${color.light};
  height: 0;
`
const TitleCont = styled.div`
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  text-transform: capitalize;
  text-align: center;
  color: ${color.light};
  margin-bottom: 2rem;
`
const Title = styled.h2`
  opacity: 0;
  animation-delay: 1s;
`
const BodyCont = styled.div`
  flex-grow: 1;
  display: flex;
  align-items: center;
  animation-delay: 1.5s;
`
export default ()=> {
  const [carousel, setCarousel] = useObserver();
  useEffect(()=>{
    if(carousel){
      gsap.to('#tourLine', 1.5, { height: "5rem" });
    } else {
      gsap.set('#tourLine', { height: 0 })
    }
  },[carousel])
  return(
    <MainCont ref={el => setCarousel(el)} id="tours">
      <TitleLine id="tourLine" />
      <TitleCont>
        <Title className={carousel && "animated fadeIn"}>especials packges</Title>
      </TitleCont>
      <BodyCont className={ carousel && "animated fadeIn" }>
        {
          carousel && <Carousel />
        }
      </BodyCont>
    </MainCont>
  )
}
