import React, { useState } from 'react';
import { color, smallShodow } from '../constants';
import styled from 'styled-components'
import { Container } from 'react-bootstrap';
import { useScroll, useMedia } from '../hooks';
import Logo from './logo';
import { ReactComponent as Bars } from '../images/icons/menu.svg';
import Modal from './modal';
import Drawer from './drawer';
import NavBar from './navbar';

const gradientNav = 'linear-gradient(rgba(208, 214, 0, .9), rgba(208, 214, 0, .9))';

const HeaderCont = styled.header`
  position: fixed;
  top: 0;
  left: 0;
  width: 100vw;
  z-index: 200;
  padding: .5rem 0;
  color: ${color.light};
  transition: 500ms ease;
  background: ${({ movil }) => movil ? color.prim : 'transparent'};
  //box-shadow: ${({ movil, down }) => movil ? smallShodow : down ? smallShodow : 'none'};
  ::before{
    position: absolute;
    top: 0;
    left: 0;
    width: 100vw;
    transition: 300ms ease;
    height: ${({ movil, down }) => movil ? 0 : down ? '66px' : 0};
    box-shadow: ${smallShodow};
    background: ${gradientNav};
    content: " ";
    z-index: -1;
  }
`
const MenuButton = styled.button`
  background: transparent;
  border: none;
  color: ${color.light};
  padding: .2rem .3;
  outline: none !important;
  &:active{
    background: ${color.phantom};
  }
`
export default ()=> {
  const movil = useMedia('xs');
  const down = useScroll();
  const [menu, setMenu] = useState(false);
  return(
    <HeaderCont className="animated fadeInDown delay-1s" down={down} movil={movil}>
      <Modal isOpen={menu} />
      <Drawer visible={menu} setVisible={setMenu}/>
      <Container className="d-flex justify-content-between align-items-center">
        <Logo />
        {
          movil && (
            <MenuButton onClick={()=> setMenu(true)}>
              <Bars />
            </MenuButton>
          )
        }
        {
          !movil && <NavBar down={down} />
        }
      </Container>
    </HeaderCont>
  )
}
