import { useState, useEffect } from 'react';

export default (query)=> {
  //const [query, setQuery] = useState('xs');
  const [device, setDevice] = useState(window.innerWidth < 572 ? true : false);
  const handleWidth = ()=>{
    const width = window.innerWidth;
    switch(query){
      case 'xs':
        return width < 576 ? setDevice(true) : setDevice(false);
      case 'sm':
        return width > 576 ? setDevice(true) : setDevice(false);
      case 'md':
        return width > 768 ? setDevice(true) : setDevice(false);
      case 'lg':
        return width >992 ? setDevice(true) : setDevice(false);
      case 'xl':
        return width > 1200 ? setDevice(true) : setDevice(false);
      default:
        return width < 576 ? setDevice(true)  : setDevice(false);
    }
  }
  useEffect(()=>{
    window.addEventListener('resize', handleWidth);
    return ()=> window.removeEventListener('resize', handleWidth);
  }, []);
  return device;
}
