import React from 'react';
import { color, font, lorem } from '../constants';
import styled from 'styled-components';
import { Row, Col } from 'react-bootstrap';
import banner from '../images/banner.jpg';
import { useObserver } from '../hooks';

const BannerCont = styled.div`
  background-color: ${color.prim};
  background-image: url(${banner});
  background-position: right;
  background-size: cover;
  background-repeat: no-repeat;
  overflow: hidden;
`
const InfoCont = styled.div`
  display: flex;
  flex-direction: column;
  min-height: 50vh;
  justify-content: center;
  align-items: flex-start;
  padding: 1rem;
  background-color: rgba(0, 0, 0, .5);
  color: ${color.light};
  opacity: 0;
  @media(min-width: 576px){
    align-items: flex-end;
  }
`
const MainTitle = styled.h2`
  text-transform: uppercase;
  font-family: ${font.common};
  width: 50%;
`
const Title = styled.h3`
  text-transform: capitalize;
  font-weight: bold;
  width: 50%;
`
const Info = styled.p`
  font-size: 11px;
  width: 50%;
`
const GreenInfo = styled.p`
  color: ${color.prim};
  font-weight: bold;
  width: 50%;
`

export default ()=> {
  const [observed, setObserved] = useObserver(0.5);
  return(
    <BannerCont ref={(el) => setObserved(el)}>
        <Row>
          <Col xs={12} sm={6}>
            <InfoCont className={observed && 'animated fadeInLeft'}>
              <MainTitle>20% off</MainTitle>
              <Title>thailand cruise adventure</Title>
              <Info>{lorem.lg}</Info>
              <GreenInfo>08 days from $1500 per person</GreenInfo>
            </InfoCont>
          </Col>
          <Col xs={12} sm={6}/>
        </Row>
    </BannerCont>
  );
}
